* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
  background-color: none;
}

.hourly .hourly-item,
.daily .daily-item {
  margin-right: 4rem;
}
@media only screen and (min-width: 768px){

}

@media only screen and (max-width: 768px) {
  .currtemp{
    font-size: 3em;
  }
  .nav-tab {
    margin-top: 1rem;
  }
  .search-btn {
    display: none;
  }
  .btn-group,
  .btn-temp {
    width: 100%;
  }
  .feel .additional {
    display: none;
  }
  img {
    height: 75px;
    width: 89px;
  }
  .hourly-item, .daily-item{
    margin-right: 2rem!important;
  }
  .hourly-item > h4, .daily-item > h4, .daily-item > h5{
    font-size: large;
  }
}
.hourly,
.daily {
  overflow-x: scroll;
}
.navbar-toggler {
  border: 0px;
}
body {
  background-color: rgb(172, 208, 255);
}
.list-group .list-group-item {
  background-color: transparent;
  color: white;
}
.btn-group .btn-check {
  color: red;
}
.btn-group .btn {
  color: brown;
}
.navbar-expand-lg:focus-visible {
  background-color: rgb(29, 28, 28);
}

@media only screen and (min-width: 992px) {
  .hourly::-webkit-scrollbar,
  .daily::-webkit-scrollbar {
    display: none;
  }
  .navbar-expand-lg {
    padding: 0.7rem 0rem;
    background-color: rgb(29, 28, 28);
  }
  .navbar-expand-lg .navbar-collapse {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-tab {
    margin-right: 1.5rem;
  }
}
.list-group-item {
  border: 0px;
}
